<template>
	<div class="gov-area-panel" v-loading="loading" element-loading-spinner="iconfont icon-loading1" element-loading-background="rgba(0, 0, 0, 0.2)">
		<area-panel area-title="在田种类统计" @hand-sub-title="handlePage">
			<div slot="panelMain" class="module-body">
				<div class="echart-container" ref="EContainer" v-show="hasData"></div>
				<div class="no-data" v-show="!hasData"><span>暂无数据</span></div>
			</div>
		</area-panel>
	</div>
</template>

<script>
import areaPanel from '@/components/overview/area-panel';
import {areaLevelCorAreaId, maxDecimalPlaces} from '@/common/fn';

// echarts 超级变量含义 {a} series.name, {b} data.name {c} data.value {d} data%
const barOption = {
	grid: {
		show: true,
		borderColor: '#0D394A',
		top: 40,
		bottom: 56,
		left: 45,
		right: 20
	},
	tooltip: {
		show: true,
		trigger: 'item',
		backgroundColor: '#043250',
		padding: [13, 14, 13, 11],
		textStyle: {
			fontSize: 12,
			color: '#9ED2D8'
		},
		formatter(a) {
			return `${a.name}：${a.value}万亩`;
		}
	},
	/*  dataZoom: [
    /!* {
      show: false,
      start: 0,
      end: 90
    },
    {
      type: 'inside',
      xAxisIndex: [0],
      start: 0,
      end: 90
    } *!/
  ], */
	xAxis: {
		data: [],
		axisLabel: {
			interval: 0,
			textStyle: {
				color: '#6EC2CB',
				fontSize: 12,
				lineHeight: 14
			},
			align: 'center',
			// rotate: -40,
			// margin: 25,
			formatter(name) {
				return name.split('').join('\n');
			}
		},
		axisTick: {
			show: false
		},
		axisLine: {
			show: true,
			lineStyle: {
				// color: '#0D394A'
				color: 'rgba(109,193,203,0.2)'
			}
		}
	},
	yAxis: {
		name: '(万亩)',
		nameTextStyle: {
			fontSize: 12,
			color: '#6DC1CB',
			padding: [0, 0, 3, 0]
		},
		axisLine: {
			show: false,
			lineStyle: {
				color: '#0D394A'
			}
		},
		axisTick: {
			show: false
		},
		splitLine: {
			show: true,
			lineStyle: {
				// color: '#0D394A'
				color: 'rgba(109,193,203,0.2)'
			}
		},
		axisLabel: {
			textStyle: {
				color: '#6DC1CB',
				fontSize: 12
			}
		}
	},
	series: [
		{
			type: 'bar',
			barWidth: '40%',
			barMinHeight: 5,
			data: [],
			label: {
				show: false
			},
			z: 10,
			itemStyle: {
				color: {
					type: 'linear',
					x: 0,
					y: 0,
					x2: 0,
					y2: 1,
					colorStops: [
						{
							offset: 0,
							color: '#00F6FF' // 0% 处的颜色
						},
						{
							offset: 1,
							color: '#01C1FF' // 100% 处的颜色
						}
					],
					global: false // 缺省为 false
				}
			}
		}
	]
};
// const farmAreaInfo = areaLevelCorAreaId();
export default {
	name: 'farm-products-7',
	props: {
		farmAreaInfo: {
			type: Object,
			default() {
				return areaLevelCorAreaId();
			}
		}
	},
	data() {
		return {
			loading: true,
			chartInstance: null,
			hasData: true
		};
	},
	mounted() {
		this.$nextTick(() => {
			this.initMoudle();
		});
	},
	methods: {
		initMoudle() {
			this.refreshChart(this._initEcharts(this.$refs.EContainer, barOption));
		},
		refreshRender() {
			if (this.chartInstance) {
				this.chartInstance.resize();
			}
		},
		// 获取在田种类统计
		getCropTypeList() {
			return this.$ajax
				.post(this.$api.operators.cropCategorylistByArea, {
					farmAreaId: [this.farmAreaInfo.farmAreaId],
					farmAreaType: this.farmAreaInfo.farmAreaType
				})
				.then(({data}) => {
					if (data.respCode === 0) {
						console.log('data:', data.obj);
						return data.obj;
					}
				})
				.catch(e => {
					console.log(new Error('获取在田种类统计error'), e);
				});
		},
		_initEcharts(el, option) {
			const chart = this.$echarts.init(el);
			chart.setOption(option);
			return chart;
		},
		async refreshChart(chartInstance) {
			const res = await this.getCropTypeList();
			if (res.list.length === 0 || res.totalCount === 0) {
				this.hasData = false;
				this.loading = false;
				return;
			}
			// 平铺
			const tiledArr = res.list
				.map(info => info.stasticPlantAcreByCropCategory)
				.flat()
				.sort((m, n) => {
					return n.sumPlantAcre - m.sumPlantAcre;
				})
				.slice(0, 15);
			const chartOpts = {
				xAxis: {
					data: tiledArr.map(info => info.cropCategoryName)
				},
				series: [
					{
						data: tiledArr.map(info =>
							maxDecimalPlaces({
								value: info.sumPlantAcre / 10000,
								places: 4
							})
						)
					}
				]
			};
			this.chartInstance = chartInstance;
			chartInstance.setOption(chartOpts);
			this.hasData = true;
			this.loading = false;
		},
		handlePage() {
			// this.$router.push({
			// 	name: 'fieldsStatisticsList'
			// });
			this.$emit('handleClick', 'fieldsStatisticsList', '在田种类统计');
		}
	},
	computed: {
		eContainerRef() {
			return this.$refs.EContainer;
		}
	},
	components: {
		areaPanel
	}
};
</script>

<style scoped lang="less">
.gov-area-panel {
	width: 100%;
	height: 100%;
	background-color: fade(#061f36, 34%);
	.module-body {
		width: 100%;
		height: 100%;
		.echart-container {
			height: 100%;
		}
		.no-data {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 14px;
			color: #00f6ff;
			font-weight: bold;
		}
	}

	/deep/ .el-loading-spinner {
		overflow: hidden;
		.iconfont {
			display: block;
			font-size: 40px;
			color: #00f6ff;
			animation: loading 1.25s linear infinite;
			margin-top: 24px;
			margin-left: 8px;
		}
	}
}
</style>



